@font-face {
  font-family: "Roboto Condensend";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400; }

@font-face {
  font-family: "Roboto Condensend";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: "Roboto Condensend";
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: "Bebas Neue";
  src: url("../fonts/BebasNeue-Regular.woff2") format("woff2"), url("../fonts/BebasNeue-Regular.woff") format("woff");
  font-weight: 400; }

html {
  box-sizing: border-box;
  -moz-osx-font-smoothing: grayscale; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  font-family: "Roboto Condensend", "Arial", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  display: block;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: currentColor;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 14px;
  padding-bottom: 14px;
  background-color: transparent; }

body {
  position: relative;
  padding: 0;
  height: 100%; }

.top-wrapper {
  background-image: url("../img/background-main.png"), radial-gradient(33.97% 69.22% at 16.03% 39.06%, #003d85 99.48%, #b80027 99.03%);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  padding-bottom: 44px; }

.header {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  padding-top: 28px;
  padding-right: 2px;
  width: 84.91%;
  max-width: 1160px; }

.header a:hover {
  opacity: 0.5; }

.header__logo {
  padding-top: 3px; }

.header__phone {
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.03em;
  text-decoration: none;
  color: #ffffff; }

.main-block {
  font-family: "Bebas Neue", "Arial", sans-serif;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  width: 84.91%;
  max-width: 1160px;
  color: #ffffff; }

.main-block img {
  margin-top: -4px; }

.main-block__main {
  display: flex;
  flex-direction: column;
  margin-left: 19px;
  margin-right: 64px;
  margin-bottom: 51px;
  width: 377px; }

.main-block__main h2 {
  font-weight: 700;
  text-align: right;
  font-size: 109px;
  line-height: 109px;
  letter-spacing: 0.035em;
  margin-top: 7px;
  margin-bottom: 0; }

.main-block__city {
  text-align: right;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  margin-top: 112px;
  margin-bottom: 0;
  width: 100%;
  order: -1; }

.main-block__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  font-family: "Roboto Condensend", "Arial", sans-serif;
  font-weight: 400;
  text-align: right;
  font-size: 14px;
  line-height: 21px;
  margin-top: -85px;
  margin-right: 264px;
  margin-bottom: 74px; }

.main-block__button {
  border: 4px solid #ffffff;
  padding-top: 20px;
  width: 232px;
  min-height: 60px;
  margin-left: auto;
  margin-top: 10px; }

.main-block__socials {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  margin-left: auto;
  margin-top: auto;
  margin-bottom: 33px; }

.main-block__socials li {
  margin-bottom: 15px; }
